#Planos{
    width: 100vw;
    right: 0;
    position: absolute;

    #engine {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;

      width: 100%;
      height: 100vh;
      filter: blur(5);
      background-color: #000000bb;

      position: fixed;
      top: 0;
      right: 0;
      z-index: 22;

      .beneficios-engine {
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        align-items: center;

        min-width: 90vw;
        height: 60vh;
        position: fixed;
        left: 100px;
        gap: 20px;
        overflow: hidden;

        .bene {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;

            width: 1200px;
            height: 100%;
            padding: 80px;
            gap: 10px;
            border-radius: 10px;
            background: linear-gradient(90deg, rgb(127, 52, 165), rgb(66, 66, 184));

            > p {
              max-width: 50ch;
              text-align: flex-start;
              padding: 10px;
              background-color: rgba(139, 70, 174, 0.163);
              border: solid 3px rgb(132, 71, 190);
              border-radius: 10px;
            }

            .swiper {
              width: 100%;
              height: 100%;
            }
            
            .swiper-slide {
              text-align: center;
              font-size: 18px;
              background: #70161600;
              display: flex;
              justify-content: center;
              align-items: center;

              img, video {
                display: block;
                width: 100%;
                height: 100%;
                object-fit: cover;
              }
            }
        }   
      }
    }

    .navegacao-swiper {
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;

      width: 100vw;
      height: 100px;
      gap: 20px;

      position: fixed;
      left: 0;
      bottom: 0;

      .sair {
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;

        width: 40px;
        height: 40px;
        padding: 6px;
        background-color: rgba(231, 231, 231, 0);
        border-radius: 10px;
        transition: 0.3s;
        border: solid 2px white;

        > img {
          height: 100%;
        }

        &:hover {
          padding: 2px;
        }
      }

      button {
        width: 200px;
        height: 40px;
        background-color: rgb(231, 231, 231);
        border: none;
        border-radius: 10px;
        transition: 0.3s;

        &:hover {
          background-color: #00000000;
          border: solid 2px white;
          color: white;
        }
      }
    }

    .barrinha  {
      width: 100%;
      height: 3px;
      border-radius: 10px;
      background-color: rgb(53, 53, 53);
    }

    .a {
      background-color: rgba(19, 158, 201, 0.479)
    }

    .b {
      background-color: rgba(132, 18, 177, 0.479)
    }

    .c {
      background-color: rgba(167, 167, 167, 0.479)
    }

      .explain{
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: flex-start;
        
        width: 100vw;
        height: 33rem; 
        padding: 30px 20px 20px 120px;

        .explain-1{
          display: flex;
          flex-wrap: wrap;
          flex-direction: row;
       
          width: 100%;
          height: 100%;
          justify-content: space-around;
          margin: 0;
          padding: 0; 
          gap: 20px;

          .explain-part1{
            display:flex;
            

            .explain-1-benefis{
              display: block;
              width: 100%;
              border-radius: 10px;
              background-color: rgba(41, 41, 41, 1);
              text-align: flex-start;
              padding: 15px;
              height: 100%;
            }
          }
          .explain-part2{
            display:flex;
            flex-direction: row;
            justify-content: space-around;
            gap: 20px;
          
            .explain-1-jogos{
              display: block;
              width: 100%;
              padding: 15px;
              background: linear-gradient(180deg, #a146d6, #63097a);
              border-radius: 10px;
            }

            .explain-1-engine{
              display: block;
              width: 100%;
              padding: 15px;
              background-color: rgba(64, 133, 197, 1);
              border-radius: 10px;
            }
          }
          
        }
        .explain-2{
          display: flex;
          width: 100%;
          height:100%;
          align-items: center;
          justify-content: center;

          .explain-2-logo{
            display: flex;
          }
        }

        .explain-3{
          display: flex;
          width: 100%;
          height: 100%;
          align-items: center;
          justify-content: center;
          
          .explain-3-tools{
            display: flex;
            background-color: rgb(77, 119, 181);
            background-image:url('../../../../public/assets/images/GameSync/Rectangle\ 860.png') ;
            background-repeat: no-repeat;
            background-size: cover;
            background-position: center center;
            width: 100%;
            height: 100%;
            justify-content: baseline;
            align-items: flex-end;
            padding: 20px;
            font-size: larger;
            border-radius: 10px;
            cursor: pointer;
          }
          
          .explain-3-tools:hover {
            border: solid 2px white;
          }
        }
      }

      
