    .botao{
      cursor: pointer;
      transition: 0.1s;
    }

    .botao::after{
      content: '';
      width: 100%;
      height: 100%;
      filter: blur(15px);
      z-index: -1;
      position: absolute;
      left: 0;
      top: 0;
    }

    .botao:active{
      transform: scale(0.9) rotate(3deg);
  background: radial-gradient(circle farthest-corner at 10% 20%, rgb(111, 202, 41) 17.8%, rgb(11, 193, 87) 100.2%);
  transition: 0.5s;
