#AdmBarraLateral {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;

    width: 100%;

    .all{
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
        
        width: 350px;
        height: 100vh;
        padding: 30px;
        background-color: #000;
        border-radius: 5px;

        z-index: 5;
        position: fixed;
        
        top: 0;
        left: 0;
        // clip-path: polygon(0 50%, 100% 3%, 100% 99%, 0% 100%);
        /*clip-path: polygon(0 6%, 100% 0, 100% calc(100% - 6%), 0% 100%);
        /*clip-path: polygon(0 6%, 100% 0, 100% 100%, 0% 90%);*/

        .title{
            display: flex;
            justify-content: center;
            height: 9rem;

            width: 21.3rem;
            .title-conteudo{
                display: flex;
                justify-content: row;
                align-items: center;
                justify-content: center;

                width: 12rem;
                justify-content: space-evenly;
            }
            .title-conteudo > img{
                width: 26%;
            }
        }
        .opcoes{
            display: flex;
            flex-direction: column;
            width: 20rem;
            height: 50vh;
            justify-content: space-evenly;
   
            margin: 30px;
            a{
                .opcoes-z{
                    display: flex;
                    flex-direction: row;
                    justify-content: center;
                    align-items: center;
                    justify-content: space-around;
                    cursor:pointer;
    
                    &.selecionado {
                        background-image: linear-gradient(45deg, #ff6ac1, #7873f5);
                        background-size: 200% 200%;
                        background-position: right center;
                        color: #000;
                    }
                }
                .opcoes-z:hover{
                    background-image: linear-gradient(45deg, #ff6ac1, #7873f5);
                    background-size: 200% 200%;
                    background-position: right center;
                    color: #000;
                }
                .opcoes-z > img{
                    width: 40px;
                }
    
                .opcoes-z > img:hover{
                    filter: invert(100%);
                }
            }
            
        }
        .sair {
            display: flex;
            width: 20rem;
            justify-content: space-around;
    
            .sair-msm {
              display: flex;
              flex-direction: row;
              justify-content: space-between; 
              align-items: center;
              cursor: pointer;
            }
            .sair-msm > img:hover{
                filter: invert(100%);
            }

            h1 {
                margin-right: 20px; /* Ajuste conforme necessrio */
              }
          }
          .sair:hover{
            background: linear-gradient(to bottom, #FF0000, #FF5733);
                background-size: 200% 200%;
                background-position: right center;
                color: #000;
          }
    }
}
a{
    text-decoration: none;
    color: white;
}
.selecionado{
    background-color: #7873f5;
    height: 3rem;
}